@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700;800&display=swap);
.discover-item{display:flex;flex-direction:column;align-items:center;width:150px;margin:20px}@media only screen and (max-width: 500px){.discover-item{width:100%;min-width:100%;margin-right:0 !important}}.discover-item:hover .discover-item__art{-webkit-transform:scale(1.05);transform:scale(1.05)}.discover-item__art{height:150px;width:150px;border-radius:6px;background-size:cover;transition:-webkit-transform 0.15s ease-in-out;transition:transform 0.15s ease-in-out;transition:transform 0.15s ease-in-out, -webkit-transform 0.15s ease-in-out;cursor:pointer}.discover-item__title{font-size:15px;text-align:center}

.discover-block__header{display:flex;align-items:center;min-height:20px}.discover-block__header>h2{color:#CECEDC;font-size:15px;letter-spacing:1px;margin:0}.discover-block__header>span{background:#eee;flex:1 1;height:2px;display:block;margin-left:20px;margin-right:20px}.discover-block__header>div{font-size:20px;display:flex}.discover-block__header>div>svg{transition:-webkit-transform 0.2s ease-in-out;transition:transform 0.2s ease-in-out;transition:transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;cursor:pointer;color:#564FD8}.discover-block__header>div>svg:hover{-webkit-transform:scale(1.15);transform:scale(1.15)}.discover-block__header>div>svg:first-of-type{margin-right:20px}.discover-block__header__icon__disabled{color:unset;pointer-events:none}.discover-block__row{display:flex;margin-bottom:50px;width:100%;overflow-x:scroll;height:250px;scroll-behavior:smooth;margin-top:20px}.discover-block__row::-webkit-scrollbar{display:none}.discover-block__row>*{margin-right:50px}@media only screen and (max-height: 750px){.discover-block__row{margin-bottom:20px}}

.discover .animate__animated.animate__fadeIn{--animate-duration: 1.5s}

.header{height:300px;background:#FFB5A7;overflow:hidden;display:flex;align-items:center;color:white;letter-spacing:1px;font-weight:300;justify-content:space-between;padding-right:5%;min-height:300px;margin-bottom:50px;-webkit-user-select:none;-ms-user-select:none;user-select:none}@media only screen and (max-height: 775px){.header{height:100px;min-height:100px;margin-bottom:30px}}@media only screen and (max-width: 750px){.header{margin-bottom:20px}}@media only screen and (max-width: 1300px){.header{padding-left:5%;padding-right:unset}}@media only screen and (min-width: 415px) and (max-width: 600px){.header{padding:20px !important;height:100px;min-height:100px}}@media only screen and (max-width: 415px){.header{padding:20px !important;height:auto;min-height:100px}}.header>div{margin-left:auto}@media only screen and (max-width: 1300px){.header>div{margin-left:unset}}@media only screen and (max-width: 650px){.header>div{margin-left:auto;margin-right:auto}}.header>div h1,.header>div h2{font-weight:500;text-shadow:0px 4px 40px rgba(0,0,0,0.05);text-align:right}@media only screen and (max-width: 1300px){.header>div h1,.header>div h2{text-align:unset}}.header>div h1{font-size:60px;margin-bottom:10px}@media only screen and (max-width: 600px){.header>div h1{font-size:24px}}@media only screen and (max-height: 775px){.header>div h1{font-size:24px}}.header>div h2{font-size:45px;display:flex;align-items:center;justify-content:flex-end;margin-top:0}@media only screen and (max-width: 1300px){.header>div h2{justify-content:unset}}@media only screen and (max-width: 600px){.header>div h2{font-size:20px}}@media only screen and (max-height: 775px){.header>div h2{font-size:20px}}.header>div h2>svg{margin-left:10px;margin-right:10px}.header>div h2>svg:first-of-type{color:#FFD60B}.header>div h2>svg:last-of-type{color:#39383D}.header>svg{align-self:flex-end;height:150%;width:auto;-webkit-transform:scaleX(-1);transform:scaleX(-1);margin-left:-50px;margin-bottom:-60px}@media only screen and (max-height: 775px){.header>svg{height:200%;margin-left:-20px}}@media only screen and (max-width: 1300px){.header>svg{display:none}}

.sidebar{min-width:225px;width:225px;background:#564FD8;height:100%;display:flex;flex-direction:column;align-items:center;padding-top:30px;padding-bottom:30px;transition:all 0.2s ease-in-out;will-change:width, min-width}@media only screen and (max-width: 650px){.sidebar{width:75px;min-width:75px}}@media only screen and (max-width: 400px){.sidebar{width:50px;min-width:50px}}.sidebar__options{width:100%;overflow-y:scroll}.sidebar__options::-webkit-scrollbar{display:none}.sidebar__option{display:flex;color:white;align-items:center;width:100%;height:75px;min-height:75px;opacity:0.5;padding-left:40px;padding-right:40px;-webkit-user-select:none;-ms-user-select:none;user-select:none}@media only screen and (max-width: 650px){.sidebar__option{padding-left:30px}}@media only screen and (max-width: 400px){.sidebar__option{padding-left:17px}}.sidebar__option>p{margin:0;margin-left:20px;font-size:18px}@media only screen and (max-width: 650px){.sidebar__option>p{display:none}}.sidebar__option--selected{opacity:1}.sidebar__option--selected{pointer-events:none;background:linear-gradient(to right, #7974DD, transparent 95%)}.sidebar__profile{width:100%;display:flex;flex-direction:column;align-items:center;color:white;font-weight:300;margin-bottom:50px;margin-top:20px}@media only screen and (max-width: 650px){.sidebar__profile>p{display:none}}.sidebar__profile>svg{width:30%;height:auto;box-shadow:0px 4px 20px rgba(0,0,0,0.1);border-radius:50%}

.player{position:absolute;width:calc(100% - 225px);background:rgba(255,255,255,0.95);height:100px;bottom:0;right:0;-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);box-shadow:0px 4px 20px rgba(0,0,0,0.1);display:flex;justify-content:space-between;align-items:center;padding-left:40px;padding-right:40px;transition:height 0.2s ease-in-out}@media only screen and (max-width: 650px){.player{width:calc(100% - 75px);padding-left:20px;padding-right:20px}}@media only screen and (max-width: 400px){.player{width:calc(100% - 50px)}}@media only screen and (max-height: 775px){.player{height:70px}}.player svg{transition:-webkit-transform 0.2s ease-in-out;transition:transform 0.2s ease-in-out;transition:transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;cursor:pointer}.player svg:hover{-webkit-transform:scale(1.15);transform:scale(1.15)}.player__album{display:flex}@media only screen and (max-width: 450px){.player__album{display:none}}.player__album>span{margin-right:20px;width:50px;height:50px;background:#CECEDC;border-radius:6px;display:block}.player__album>p{font-weight:500;color:#39383D;font-size:17px}@media only screen and (max-width: 1000px){.player__album>p{display:none}}.player__controls{margin-left:50px;display:flex;align-items:center;position:relative;justify-content:center}@media only screen and (max-width: 850px){.player__controls{margin-left:0}}.player__controls>svg:nth-of-type(1),.player__controls>svg:nth-of-type(3){color:#CECEDC}.player__controls>svg:nth-of-type(2){color:#564FD8;font-size:35px;margin-left:15px;margin-right:15px;z-index:1;position:relative}.player__controls:after{content:'';width:25px;height:25px;position:absolute;border-radius:50%;z-index:0;border:10px solid #564FD8;background:white;box-sizing:content-box !important}.player__seekbar{flex:1 1;height:4px;background:#CECEDC;margin-left:50px;margin-right:50px;border-radius:2px}@media only screen and (max-width: 850px){.player__seekbar{margin-left:20px;margin-right:20px}}.player__actions{color:#CECEDC}.player__actions>.fa-ellipsis-h{display:none;margin-right:0 !important}@media only screen and (max-width: 750px){.player__actions>svg:not(.fa-ellipsis-h){display:none;width:0 !important;margin:0}.player__actions>.fa-ellipsis-h{display:unset}}.player__actions>svg:not(:last-of-type){margin-right:30px}@media only screen and (max-width: 800px){.player__actions>svg:not(:last-of-type){margin-right:15px}}

*{box-sizing:border-box}a,p,h1,h2,h3,button{font-family:'Rubik', sans-serif}body{margin:0;background:#F9F8FF}html,body,#root{height:100%}.main{height:100%;display:flex;border-radius:12px;overflow:hidden;box-shadow:0px 4px 20px rgba(0,0,0,0.1);position:relative;background:white}.main__content{display:flex;flex-direction:column;flex:1 1;width:calc(100% - 225px)}.main__content__child{display:flex;flex-direction:column;flex:1 1;padding:50px;padding-top:0;padding-bottom:100px;overflow-y:scroll}@media only screen and (max-width: 750px){.main__content__child{padding:20px}}

